.btn {
  display: inline-block;
  vertical-align: top;

  padding: 0 10px;
  margin: 0;
  border: none;
  min-width: 40px;
  height: 40px;

  color: $darkGray;

  @include rlight;
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  text-decoration: none;

  white-space: nowrap;

  transition: background 0.175s ease, box-shadow 0.175s ease;

  cursor: pointer;
  user-select: none;
  @include active {
    color: inherit;
    box-shadow: inset 2px 2px 6px rgba($black, 0.2); }
  .icon {
    vertical-align: top; }

  //mods
  &_lg {
    padding: 0 15px;
    min-width: 60px;
    height: 60px;
    font-size: 19px;
    line-height: 60px;
    .icon {
      margin-top: 10px; }
    .icon-more-v {
      margin-top: 0; } }
  &_xl {
    padding: 0 20px;
    min-width: 80px;
    height: 80px;
    font-size: 30px;
    line-height: 80px; }

  &_block {
    display: block;
    width: 100%; }

  &_icon {
    padding: 0;
    background: none; }

  &_rounded {
    border-radius: 5px; }

  &_green,
  &_success {
    color: $white;
    background: $green;
    @include active {
      color: $white;
      background: $greenActive; } }
  &_red,
  &_error {
    color: $white;
    background: $red;
    @include active {
      color: $white;
      background: $redActive; } }
  &_default {
    color: $darkGray;
    background: $lightGray;
    @include active {
      color: $darkGray;
      background: $lightGrayActive; } }
  &_primary {
    color: $white;
    background: $blue;
    @include active {
      color: $white;
      background: $blueActive; } }
  &_secondary {
    color: $blue;
    background: $azure;
    @include active {
      color: $blue;
      background: $azureActive; } }
  &_white {
    color: inherit;
    background: $white;
    @include active {
      background-color: $eb; } } }

button {
  &:disabled,
  &.is-disabled {
    opacity: 0.5;
    pointer-events: none; } }

.btn-icon {
  display: inline-block;
  vertical-align: top;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  color: inherit;
  background: none;
  text-decoration: none;
  transition: background 0.175s ease;
  @include active {
    color: inherit;
    background: rgba($black, 0.1); }
  &_square {
    border-radius: 0; }
  &_gray {
    background: $lightGray;
    @include active {
      background: $lightGrayActive; } } }

.btn-link {
  color: inherit;
  background: none;
  font-family: inherit;
  font-size: inherit;
  @include active {
    color: inherit; } }

.btn-back {
  color: inherit;
  background: none;
  @include rlight;
  font-size: 16px;
  transition: background 0.175s ease, box-shadow 0.175s ease;
  cursor: pointer;
  @include active {
    color: inherit;
    background: rgba($black, 0.1);
    box-shadow: inset 2px 2px 6px rgba($black, 0.2); }
  &__icon,
  &__title {
    display: inline-block;
    vertical-align: top; }
  &__icon {
    width: 50px;
    height: 50px;
    line-height: 50px; }
  &__title {
    padding: 0 20px 0 0;
    line-height: 50px; } }
////
.btn-lg {
  padding: 0 15px;
  min-width: 60px;
  height: 60px;
  font-size: 19px;
  line-height: 60px; }

.btn-xl {
  padding: 0 20px;
  min-width: 80px;
  height: 80px;
  font-size: 30px;
  line-height: 80px; }

.btn-block {
  display: block;
  width: 100%; }

.btn-rounded {
  border-radius: 5px; }

.btn-go {
  width: 300px; }

input.btn {
  -webkit-appearance: none;
  -webkit-border-radius: 0; }

.btn-default {
  color: $darkGray;
  background: $lightGray;
  @include active {
    background: $lightGrayActive; } }

.btn-primary {
  color: $white;
  background: $blue;
  @include active {
    background: $blueActive; } }

.btn-secondary {
  color: #2e99db;
  background: #ecf7fd;
  @include active {
    background: #c7e7f9; } }

.btn-danger {
  color: #fff;
  background: #FF6431;
  @include active {
    background: #ff4508; } }

.btn-green, .btn-success {
  color: #fff;
  background: #66BB6A;
  @include active {
    background: #4cac51; } }

.btn-white {
  color: inherit;
  background: #fff;
  @include active {
    background-color: #ebebeb; } }

.animated {
  .btn-ripple {
    position: relative;
    overflow: hidden; }
  .btn-ripple__effect {
    position: absolute;
    z-index: 100;
    top: 50%;
    left: 50%;
    margin: -10px 0 0 -10px;
    border-radius: 50%;
    width: 20px;
    height: 20px;

    opacity: 0.05;
    background: black;

    transform: scale(0);
    transition: transform 500ms ease-out, opacity 500ms ease-out;
    pointer-events: none;
    &.effect-on {
      opacity: 0;
      transform: scale(2); } }
  .btn-ripple {
    @include active {
      box-shadow: none; } } }

.btn.icon {
  padding: 0;
  min-width: 0;
  color: inherit;
  background: transparent; }

